@import url(https://fonts.googleapis.com/css2?family=Averia+Libre:wght@700&display=swap);
html,
body,
#root {
  font-size: 16px;
  overflow: auto;
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  overflow: auto;
  width: 100%;
  height: 100%;
}

.App {
  background-color: #323232;
  display: flex;
  align-items: center;
  justify-content: center;
}
.instruction-text {
  font-size: 1.5rem;
  font-weight: bold;
  display: block;
  color: white;
}

.button {
  cursor: pointer;
  background-color: #2a54ef;
  color: white;
  align-items: center;
  text-align: center;
  border-radius: 5px;
  font-size: 1.5rem;
  font-weight: bold;
  border: 0;
  padding: 0.125em 0.8em;
  margin: 0.25em;
  transform: scale(1);
  transition: transform 0.25s ease-in-out;
}

.button:hover {
  transform: scale(1.1);
}

.button:active {
  transform: scale(1);
}

.host.button {
  background-color: darkred;
}

.player.button {
  background-color: green;
}

.bingo-effect-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
}

.bingo-effect-container .text {
  font-family: "Averia Libre", cursive;
  font-size: 3.5rem;
  color: orangered;
  text-shadow: 0 0 5px #ff5151;
  text-align: center;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.confetti {
  width: 15px;
  height: 15px;
  background-color: #f2d74e;
  position: absolute;
  left: 50%;
  animation: confetti 5s ease-in-out -2s infinite;
  transform-origin: left top;
}
.confetti:nth-child(1) {
  background-color: #f2d74e;
  left: 10%;
  animation-delay: 0;
}
.confetti:nth-child(2) {
  background-color: #95c3de;
  left: 20%;
  animation-delay: -5s;
}
.confetti:nth-child(3) {
  background-color: #ff9a91;
  left: 30%;
  animation-delay: -3s;
}
.confetti:nth-child(4) {
  background-color: #f2d74e;
  left: 40%;
  animation-delay: -2.5s;
}
.confetti:nth-child(5) {
  background-color: #95c3de;
  left: 50%;
  animation-delay: -4s;
}
.confetti:nth-child(6) {
  background-color: #ff9a91;
  left: 60%;
  animation-delay: -6s;
}
.confetti:nth-child(7) {
  background-color: #f2d74e;
  left: 70%;
  animation-delay: -1.5s;
}
.confetti:nth-child(8) {
  background-color: #95c3de;
  left: 80%;
  animation-delay: -2s;
}
.confetti:nth-child(9) {
  background-color: #ff9a91;
  left: 90%;
  animation-delay: -3.5s;
}
.confetti:nth-child(10) {
  background-color: #f2d74e;
  left: 100%;
  animation-delay: -2.5s;
}

@keyframes confetti {
  0% {
    transform: rotateZ(15deg) rotateY(0deg) translate(0, 0);
  }
  25% {
    transform: rotateZ(5deg) rotateY(360deg) translate(-5vw, 20vh);
  }
  50% {
    transform: rotateZ(15deg) rotateY(720deg) translate(5vw, 60vh);
  }
  75% {
    transform: rotateZ(5deg) rotateY(1080deg) translate(-10vw, 80vh);
  }
  100% {
    transform: rotateZ(15deg) rotateY(1440deg) translate(10vw, 110vh);
  }
}
.animate-text span {
  display: inline-block;
  opacity: 0;
  transform: translate(-150px, -50px) rotate(-180deg) scale(3);
  animation: revolveScale 0.4s forwards;
}

.animate-text .bingo-count {
  margin: 0 15px;
  font-size: 4.5rem;
}

.animate-text .bingo-count > .x {
  font-size: 2.5rem;
}

.animate-text span:nth-of-type(2) {
  animation-delay: 0.05s;
}
.animate-text span:nth-of-type(3) {
  animation-delay: 0.1s;
}
.animate-text span:nth-of-type(4) {
  animation-delay: 0.15s;
}
.animate-text span:nth-of-type(5) {
  animation-delay: 0.2s;
}
.animate-text span:nth-of-type(6) {
  animation-delay: 0.25s;
}
.animate-text span:nth-of-type(7) {
  animation-delay: 0.3s;
}
.animate-text span:nth-of-type(8) {
  animation-delay: 0.35s;
}
.animate-text span:nth-of-type(9) {
  animation-delay: 0.4s;
}
.animate-text span:nth-of-type(10) {
  animation-delay: 0.45s;
}
.animate-text span:nth-of-type(11) {
  animation-delay: 0.5s;
}
.animate-text span:nth-of-type(12) {
  animation-delay: 0.55s;
}
.animate-text span:nth-of-type(13) {
  animation-delay: 0.6s;
}
.animate-text span:nth-of-type(14) {
  animation-delay: 0.65s;
}
.animate-text span:nth-of-type(15) {
  animation-delay: 0.7s;
}
.animate-text span:nth-of-type(16) {
  animation-delay: 0.75s;
}
.animate-text span:nth-of-type(17) {
  animation-delay: 0.8s;
}
.animate-text span:nth-of-type(18) {
  animation-delay: 0.85s;
}
.animate-text span:nth-of-type(19) {
  animation-delay: 0.9s;
}
.animate-text span:nth-of-type(20) {
  animation-delay: 0.95s;
}

@keyframes revolveScale {
  60% {
    transform: translate(20px, 20px) rotate(30deg) scale(0.3);
  }

  100% {
    transform: translate(0) rotate(0) scale(1);
    opacity: 1;
  }
}

.bingo-container {
  width: 100%;
  max-width: 650px;
  margin: auto;
  border: 1px solid #121212;
  padding: 10px;
  background-color: #ebebeb;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(5, minmax(0, 1fr));
  grid-auto-rows: 1fr;
}

.grid-container::before {
  content: "";
  width: 0;
  padding-bottom: 100%;
  grid-row: 1 / 1;
  grid-column: 1 / 1;
}
.grid-container > .grid:first-child {
  grid-row: 1 / 1;
  grid-column: 1 / 1;
}

.grid-container > .grid {
  background-color: white;
  border: 1px #ebebeb solid;
  transition: transform 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
}

.grid-container > .grid:not(.selected):hover {
  cursor: pointer;
  transform: scale(1.2);
}

.grid-container > .grid.selected {
  text-decoration: line-through;
  background-color: #e1f9ff;
}

.grid-container > .grid.selected.bingo {
  text-decoration: line-through;
  background-color: lightgoldenrodyellow;
}

.used-content-container {
  color: white;
  font-size: 25px;
  text-align: center;
}
.used-content-list > span {
  display: inline-block;
  margin: 5px 10px;
}
.latest-pick {
  display: block;
  font-weight: bold;
  color: orangered;
  font-size: 60px;
}
.used-content-list > span:last-child {
  color: orangered;
  font-weight: bold;
}

.used-content-container .button {
  margin: 20px auto;
}

